.cart {
    padding-top: 120px;
    padding-bottom: 50px;
    min-height: 100vh;
    &__btn {
        padding: 20px;
        width: 250px;
        background-color: #27ae60;
        border: none;
        display: block;
        cursor: pointer;
        margin: 20px auto;
        text-transform: uppercase;
        color: #fff;
        font-size: 1.15rem;
        border-radius: 5px;
    }
    &__empty {
        font-size: 36px;
        color: rgba(255, 255, 255, 0.692);
        text-align: center;
        position: absolute;
        top: 50%;
        letter-spacing: 10px;
        left: 50%;
        transform: translate(-50%, -50%);

    }
    &__title {
        font-size: 36px;
        color: #fff;
        text-align: center;
        font-weight: 300;
    }
    &__list {
        width: 60%;
        margin: 0 auto;
    }
    &__item {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 40px;
        border: 1px solid rgba(256,256,256, .2);
        background-color: rgba(0,0,0,.7);
        box-shadow: 0 0 20px rgba(256,256,256, .1);
        &-title, &-price  {
            width: 250px;
            text-align: center;
            color: #fff;
            font-size: 24px;
        }
        &-img {
            height: 125px;
            width: 250px;
            object-fit: cover;
        }
        &-counter {
            width: 50px;
            height: 50px;
            padding: 15px 0;
            border-radius: 100%;
            font-size: 20px;
            background-color: #fff;
            text-align: center;
            position: absolute;
            right: -20px;
            bottom: -20px;
        }
    }
    &__close {
        position: absolute;
        width: 25px;
        height: 25px;
        text-align: center;
        top: -10px;
        right: -10px;
        color: #fff;
        font-size: 20px;
        border-radius: 100%;
        border: 1px solid #fff;
        cursor: pointer;
    }
}